.Footer {
	width: 100%;
	height: 100%;
	min-height: 64px;
}

.Footer::before {
	content: '';
	display: block;
	height: 1px;
	width: 99vw;
	background-color: #F0F0F0;
	position: absolute;
	top: 0;
	left: 50%;
	-webkit-transform: translate(-50%);
	    -ms-transform: translate(-50%);
	        transform: translate(-50%);
}

@media screen and (max-width: 768px) {
	.Footer::before {
		display: none;
	}
}

.Page-Footer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	position: relative;
}

.FooterArea {
  width: 100%;
	height: 100%;
	margin: 0 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

.FooterArea-Nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

@media screen and (max-width: 768px) {
  .FooterArea {
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-align: start;
		    align-items: flex-start;
		margin-bottom: 15px;
	}

	.FooterArea-Nav {
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-order: 2;
		    order: 2;
	}
}



