.Camera-Image {
  width: 100%;
	height: 100%;
	min-height: 325px;
	border-radius: 20px;
	background-repeat: repeat;
	background-position: center;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	-ms-touch-action: none;
	    touch-action: none;  
}

@media screen and (max-width: 570px) {
	.Camera-Image {
		min-height: 250px;
	}
}


