	background-color: #979797;
	height: 1px;
}

.Player-Range_time::-moz-range-track {
	background-color: #979797;
	height: 1px;
}

.Player-Range_time::-webkit-slider-thumb {
	-webkit-appearance: none;
	        appearance: none;
	width: 12px;
	height: 20px;
	cursor: pointer;
	background: #D8D8D8;
	border-radius: 4.5px;
	margin-top: -10px;
}

.Player-Range_time::-moz-range-thumb {
	-moz-appearance: none;
	     appearance: none;
	width: 12px;
	height: 20px;
	cursor: pointer;
	background: #D8D8D8;
	border-radius: 4.5px;
	margin-top: -10px;
}


