.Header {
  width: 100%;
  height: 100%;
  min-height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.Header::after {
	content: '';
	display: block;
	height: 1px;
	width: 99vw;
	background-color: #F0F0F0;
	position: absolute;
	bottom: 0;
	left: 50%;
	-webkit-transform: translate(-50%);
	    -ms-transform: translate(-50%);
	        transform: translate(-50%);
}

@media screen and (max-width: 768px) {
	.Header::after {
		display: none;
	}
}

.HeaderArea {
	width: 100%;
	height: 100%;
	margin: 0 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}
@media screen and (max-width: 1370px) {
	margin: 0 calc(20px + (40 - 20) * ((100vw - 375px) / (1370 - 375)));
}

@media screen and (max-width: 750px) {
	.HeaderArea {
		-ms-flex-pack: justify;
		    justify-content: space-between;
	}
	.HeaderArea-Nav {
		display: none;
	}
}

.HeaderArea-Nav {
	margin-left: 21px;
	margin-top: 8px;
}



